<template>
    <div class="home-msg">
        <div class="home-msg-cont">
            <product-name :item="{}" :code="code" :checkIdis="false"></product-name>
            <product-info
                :item="{ value: { showTrace: false } }"
                :code="code"
                :code-attrs="nonRefAttrs"
                :batch="batch"
            ></product-info>
        </div>
    </div>
</template>

<script>
const defaultIcon = require('@/assets/images/logo.png')
import { getJsonResult } from '@/utils/server-wrapper'
import ProductInfo from '@/components/product-template/ProductInfo'
import ProductName from '@/components/product-template/ProductName'

export default {
    data() {
        return {
            defaultIcon,
            userInfo: {},
            idisCode: '',
            tenantId: 0,
            batch: {},
            code: {},
            nonRefAttrs: [],
            refAttrs: [],
            attrCategoryNameList: []
        }
    },
    components: {
        ProductInfo,
        ProductName
    },
    methods: {},
    async beforeRouteEnter(to, from, next) {
        const idisCode = to.query.code
        const info = await getJsonResult(`/wx/codeInformation?codeId=${encodeURIComponent(idisCode)}`)
        const code = info.code
        const tenantId = code.tenantId
        const batch = info.batch
        const codeAttr = info.codeAttr

        next(vm => {
            vm.$data.idisCode = idisCode
            vm.$data.code = code
            // vm.$data.userInfo = userInfo
            vm.$data.tenantId = tenantId
            vm.$data.nonRefAttrs = codeAttr.filter(c => c.references.length == 0)
            vm.$data.refAttrs = codeAttr.filter(c => c.references.length > 0)
            vm.$data.batch = batch
        })
    }
}
</script>

